<template>
  <div class="wrapper">
    <el-button class="add" @click="willAdd()">添加</el-button>
    <el-select v-model="typeKw" placeholder="请选择查询的服务类型">
        <el-option label="全部" value=""></el-option>
      <el-option v-for="item in types" :key="item" :label="item" :value="item"></el-option>
    </el-select>
    <!-- list -->
    <el-table :data="tableData" height="300" border>
      <el-table-column label="序号">
        <template slot-scope="scope">{{scope.$index+1}}</template>
      </el-table-column>
      <el-table-column label="名称">
        <template slot-scope="scope">{{scope.row.name}}</template>
      </el-table-column>
      <el-table-column label="电话">
        <template slot-scope="scope">{{scope.row.tel}}</template>
      </el-table-column>
      <el-table-column label="价格">
        <template slot-scope="scope">{{scope.row.price}}</template>
      </el-table-column>
      <el-table-column label="年龄">
        <template slot-scope="scope">{{scope.row.age}}</template>
      </el-table-column>
      <el-table-column label="学历">
        <template slot-scope="scope">{{scope.row.edu}}</template>
      </el-table-column>
      <el-table-column label="服务类型">
        <template slot-scope="scope">{{scope.row.type.join(",")}}</template>
      </el-table-column>

      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button @click="edit(scope.row.id)">查看</el-button>
          <v-del :id="scope.row.id" @del="del($event)"></v-del>
        </template>
      </el-table-column>
    </el-table>

    <!-- add -->
    <el-dialog title="添加家政人员" :visible.sync="dialogFormVisible">
      <el-form>
        <el-row class="row">
          <el-col :span="3" class="text-center label">姓名</el-col>
          <el-col :span="21">
            <el-input autocomplete="off" v-model="user.name" placeholder="姓名"></el-input>
          </el-col>
        </el-row>
        <el-row class="row">
          <el-col :span="3" class="text-center label">年龄</el-col>
          <el-col :span="21">
            <el-input
              autocomplete="off"
              type="number"
              min="0"
              max="100"
              v-model="user.age"
              placeholder="0-100之间"
            ></el-input>
          </el-col>
        </el-row>
        <el-row class="row">
          <el-col :span="3" class="text-center label">电话</el-col>
          <el-col :span="21">
            <el-input autocomplete="off" v-model="user.tel" placeholder="eg:16737373737"></el-input>
          </el-col>
        </el-row>
        <el-row class="row">
          <el-col :span="3" class="text-center label">资格认证</el-col>
          <el-col :span="21">
            <el-checkbox-group v-model="user.qualification">
              <el-checkbox v-for="i in qualifications" :key="i" :label="i"></el-checkbox>
            </el-checkbox-group>
          </el-col>
        </el-row>
        <el-row class="row">
          <el-col :span="3" class="text-center label">服务项目</el-col>
          <el-col :span="21">
            <el-checkbox-group v-model="user.type">
              <el-checkbox v-for="i in types" :key="i" :label="i"></el-checkbox>
            </el-checkbox-group>
          </el-col>
        </el-row>
        <el-row class="row">
          <el-col :span="3" class="text-center label">学历</el-col>
          <el-col :span="21">
            <el-select v-model="user.edu" placeholder="请选择学历">
              <el-option
                v-for="item in edus"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-col>
        </el-row>
        <el-row class="row">
          <el-col :span="3" class="text-center label">工作年限</el-col>
          <el-col :span="21">
            <el-input
              autocomplete="off"
              type="number"
              min="0"
              v-model="user.year"
              placeholder="工作年限"
            ></el-input>
          </el-col>
        </el-row>
        <el-row class="row">
          <el-col :span="3" class="text-center label">级别</el-col>
          <el-col :span="21">
            <el-input autocomplete="off" v-model="user.vNum" placeholder="eg:v3"></el-input>
          </el-col>
        </el-row>
        <el-row class="row">
          <el-col :span="3" class="text-center label">价格</el-col>
          <el-col :span="21">
            <el-input autocomplete="off" v-model="user.price" placeholder="eg:30元/小时"></el-input>
          </el-col>
        </el-row>
        <el-row class="row">
          <el-col :span="3" class="text-center label">城市</el-col>
          <el-col :span="21">
            <el-input autocomplete="off" v-model="user.city" placeholder="eg:武汉"></el-input>
          </el-col>
        </el-row>
        <el-row class="row">
          <el-col :span="3" class="text-center label">经验</el-col>
          <el-col :span="21">
            <el-input autocomplete="off" v-model="user.experience" placeholder="eg:3年"></el-input>
          </el-col>
        </el-row>

        <el-row class="row">
          <el-col :span="3" class="text-center label">点赞数量</el-col>
          <el-col :span="21">
            <el-input
              autocomplete="off"
              type="number"
              min="0"
              v-model="user.likeNum"
              placeholder="点赞数量"
            ></el-input>
          </el-col>
        </el-row>
        <el-row class="row">
          <el-col :span="3" class="text-center label">关注人数</el-col>
          <el-col :span="21">
            <el-input
              autocomplete="off"
              type="number"
              min="0"
              v-model="user.readNum"
              placeholder="关注人数"
            ></el-input>
          </el-col>
        </el-row>

        <el-row class="row">
          <el-col :span="3" class="text-center label">距离</el-col>
          <el-col :span="21">
            <el-input autocomplete="off" v-model="user.len" placeholder="eg:4.2km"></el-input>
          </el-col>
        </el-row>

        <el-row class="row">
          <el-col :span="3" class="text-center label">自我评价</el-col>
          <el-col :span="21">
            <el-input autocomplete="off" v-model="user.info" placeholder="eg:人见人爱,花见花开。。"></el-input>
          </el-col>
        </el-row>
        <el-row class="row">
          <el-col :span="3" class="text-center label">免冠照片</el-col>
          <el-col :span="21">
            <v-upload :objImg="objImg" @changed="changed($event)"></v-upload>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取消</el-button>
        <el-button type="primary" @click="add()" v-if="!isEdit">添加</el-button>
        <el-button type="primary" @click="update()" v-if="isEdit">修改</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script type="text/ecmascript-6">
import API from "../../common/js/API";
import http from "../../common/js/ajax";
export default {
  components: {},
  props: {},
  data() {
    return {
      //服务类型检索关键字
      typeKw: "",
      //学历类型
      edus: [
        {
          label: "小学",
          value: "小学"
        },
        {
          label: "初中",
          value: "初中"
        },
        {
          label: "高中",
          value: "高中"
        },
        {
          label: "本科",
          value: "本科"
        },
        {
          label: "研究生",
          value: "研究生"
        },
        {
          label: "博士",
          value: "博士"
        }
      ],
      //证书
      qualifications: [],
      //服务类型
      types: [],
      tableData: [],
      dialogFormVisible: false,
      user: {
        img: "",
        name: "",
        year: "",
        vNum: "",
        price: "",
        city: "",
        age: "",
        edu: "",
        experience: "",
        readNum: "",
        likeNum: "",
        len: "",
        tel: "",
        info: "",
        qualification: [],
        type: []
      },
      file: null,
      name: "",
      objImg: {
        showImgUrl: ""
      },
      isEdit: false
    };
  },
  watch: {
      typeKw(){
          this.init(this.typeKw)
      }
  },
  computed: {},
  methods: {
    getQualification() {
      http(
        this,
        API.getQualification,
        "get",
        {},
        err => {},
        d => {
          this.qualifications = d.data.qualification;
        }
      );
    },
    getHomeType() {
      http(
        this,
        API.getHomeType,
        "get",
        {},
        err => {},
        d => {
          this.types = d.data.type;
        }
      );
    },
    changed(e) {
      this.file = e.file;
      this.name = e.name;
    },
    willAdd() {
      this.isEdit = false;
      this.dialogFormVisible = true;
      this.objImg.showImgUrl = "";
      this.user = {
        img: "",
        name: "",
        year: "",
        vNum: "",
        price: "",
        city: "",
        age: "",
        edu: "",
        experience: "",
        readNum: "",
        likeNum: "",
        len: "",
        tel: "",
        info: "",
        qualification: [],
        type: []
      };
    },
    add() {
      if (!this.file || !this.name) {
        this.$message({
          message: "请选择图片",
          type: "warning"
        });
        return;
      }
      qiniu
        .upload(this.file, this.name, this.$store.state.token)
        .subscribe(res => {
          if (res.total.percent == 100) {
            this.user.img = this.$store.state.qiniu + this.name;
            http(
              this,
              API.addHomeWorker,
              "get",
              this.user,
              err => {
                this.$message({
                  message: err.data.info,
                  type: "warning"
                });
              },
              d => {
                this.$message({
                  message: d.data.info,
                  type: "success"
                });
                this.dialogFormVisible = false;
                this.init();
              }
            );
          }
        });
    },
    edit(id) {
      this.isEdit = true;
      this.dialogFormVisible = true;
      this.file = null;
      http(
        this,
        API.findHomeWorker,
        "get",
        { id },
        err => {
          this.$message({
            message: err.data.info,
            type: "warning"
          });
        },
        d => {
          var user = d.data.data[0];
          if (user.type.indexOf("[") != -1) {
            user.type = JSON.parse(user.type);
            user.qualification = JSON.parse(user.qualification);
          } else {
            user.type = user.type.split(",");
            user.qualification = user.qualification.split(",");
          }
          this.user = user;
          this.objImg.showImgUrl = this.user.img;
        }
      );
    },
    del(id) {
      http(
        this,
        API.delHomeWorker,
        "get",
        { id },
        err => {
          this.$message({
            message: err.data.info,
            type: "warning"
          });
        },
        d => {
          this.$message({
            message: d.data.info,
            type: "success"
          });
          this.init();
        }
      );
    },
    update() {
      if (this.file) {
        //图片修改，需要重新上传七牛云
        qiniu
          .upload(this.file, this.name, this.$store.state.token)
          .subscribe(res => {
            if (res.total.percent == 100) {
              this.user.img = this.$store.state.qiniu + this.name;
              http(
                this,
                API.updateHomeWorker,
                "get",
                this.user,
                err => {
                  this.$message({
                    message: err.data.info,
                    type: "warning"
                  });
                },
                d => {
                  this.$message({
                    message: d.data.info,
                    type: "success"
                  });
                  this.dialogFormVisible = false;
                  this.init();
                }
              );
            }
          });
      } else {
        http(
          this,
          API.updateHomeWorker,
          "get",
          this.user,
          err => {
            this.$message({
              message: err.data.info,
              type: "warning"
            });
          },
          d => {
            this.$message({
              message: d.data.info,
              type: "success"
            });
            this.dialogFormVisible = false;
            this.init();
          }
        );
      }
    },
    init(typeKw) {
        var params={};
        if(typeKw!=""&&typeKw){
            params={
                type:typeKw
            }
        }
      http(
        this,
        API.findHomeWorker,
        "get",
        params,
        err => {
          this.$message({
            message: err.data.info,
            type: "warning"
          });
        },
        d => {
          this.tableData = d.data.data;
          this.tableData.map(item => {
            if (item.type.indexOf("[") != -1) {
              item.type = JSON.parse(item.type);
              item.qualification = JSON.parse(item.qualification);
            } else {
              item.type = item.type.split(",");
              item.qualification = item.qualification.split(",");
            }
          });
          console.log(this.tableData);
        }
      );
    }
  },
  mounted() {
    this.init();
    this.getQualification();
    this.getHomeType();
  }
};
</script>
<style lang='stylus'  scoped>
@import '../../common/stylus/index.styl';

.wrapper {
  .el-input {
    width: 90%;
    margin: 0px auto;
  }

  .el-table {
    width: 100%;
  }

  .row {
    margin: 10px 0px;

    .label {
      line-height: 40px;
    }
  }

  img {
    width: 200px;
    height: 100px;
  }
}
</style>



