<template>
  <div class="wrapper">
    <el-select v-model="waterId" placeholder="请选择查询的水站">
        <el-option
          v-for="item in waters"
          :key="item.id"
          :label="item.name"
          :value="item.id"
        ></el-option>
      </el-select>
    <el-table :data="tableData" height="250" border>
      <el-table-column label="序号">
        <template slot-scope="scope">{{scope.$index+1}}</template>
      </el-table-column>
      <el-table-column prop="name" label="用户名称"></el-table-column>
      <el-table-column prop="content" label="评论内容"></el-table-column>
      <el-table-column label="时间">
        <template slot-scope="scope">{{scope.row.time |transTime}}</template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <v-del :id="scope.row.id" @del="del($event)"></v-del>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script type="text/ecmascript-6">
import API from "../../common/js/API";
import http from "../../common/js/ajax";
export default {
  components: {},
  props: {},
  data() {
    return {
      waterId:"",
      tableData: [],
      waters:[]
    };
  },
  watch: {
    waterId(){
      this.init(this.waterId)
    }
  },
  computed: {},
  methods: {
    findWater(){
       http(
        this,
        API.findWater,
        "get",
        {},
        err => {
          this.$message({
            message: err.data.info,
            type: "warning"
          });
        },
        d => {
          this.waters = d.data.data;
           this.waters.unshift({
            id:"",
            name:"全部"
          })
        }
      );
    },
    del(id) {
      console.log(id)
      http(
        this,
        API.delComment,
        "get",
        { id },
        err => {
          this.$message({
            message: err.data.info,
            type: "warning"
          });
        },
        d => {
          this.$message({
            message: d.data.info,
            type: "success"
          });
          this.init()
        }
      );
    },
    init(id) {
      var params={};
      if(id){
        params={waterId:id}
      }
      http(
        this,
        API.findComment,
        "get",
        params,
        err => {
          this.$message({
            message: err.data.info,
            type: "warning"
          });
        },
        d => {
          this.tableData = d.data.data;
         
        }
      );
    }
  },
  mounted() {
    this.init()
    this.findWater()
  }
};
</script>
<style lang='stylus'  scoped>
@import '../../common/stylus/index.styl';

.wrapper {
  .el-input {
    width: 90%;
    margin: 0px auto;
  }
  .el-table{
    width 100%
  }

  .row {
    margin: 10px 0px;

    .label {
      line-height: 40px;
    }
  }
}
</style>



