<template>
  <div class="wrapper">
    <el-row class="row">
      <el-col :span="3" class="text-center label">账号</el-col>
      <el-col :span="21">
        <el-input :disabled="true" autocomplete="off" v-model="user.name" placeholder="请输入账号"></el-input>
      </el-col>
    </el-row>
    <el-row class="row">
      <el-col :span="3" class="text-center label">原始密码</el-col>
      <el-col :span="21">
        <el-input autocomplete="off" show-password v-model="user.oldpass" placeholder="请输入账号"></el-input>
      </el-col>
    </el-row>
    <el-row class="row">
      <el-col :span="3" class="text-center label">新密码</el-col>
      <el-col :span="21">
        <el-input autocomplete="off" show-password v-model="user.newpass" placeholder="请输入账号"></el-input>
      </el-col>
    </el-row>
    <el-row class="row">
      <el-col :span="3" class="text-center label">确认密码</el-col>
      <el-col :span="21">
        <el-input  show-password autocomplete="off" v-model="confirm" placeholder="请输入账号"></el-input>
      </el-col>
    </el-row>
    <div class="text-center">
      <el-button @click="changePass()">修改</el-button>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import API from "../../common/js/API";
import http from "../../common/js/ajax";
export default {
  components: {},
  props: {},
  data() {
    return {
      user: {
        name: localStorage.getItem("username"),
        oldpass: "",
        newpass: ""
      },
      confirm: ""
    };
  },
  watch: {},
  computed: {},
  methods: {
    changePass() {
      if (this.confirm != this.user.newpass) {
        this.$message({
          message: "新密码与确认密码不一致",
          type: "warning"
        });
        return;
      }
      http(
        this,
        API.changePassManage,
        "post",
        this.user,
        err => {
          this.$message({
            message: err.data.info,
            type: "warning"
          });
        },
        d => {
          this.$message({
            message: d.data.info,
            type: "success"
          });
          this.$router.replace("/index")
        }
      );
    }
  },
  mounted() {}
};
</script>
<style lang='stylus'  scoped>
@import '../../common/stylus/index.styl';

.wrapper {
  .el-input {
    width: 90%;
    margin: 0px auto;
  }

  .row {
    margin: 10px 0px;

    .label {
      line-height: 40px;
    }
  }
}
</style>



