<template>
  <div class="wrapper">
    <el-tabs v-model="activeName" >
      <el-tab-pane label="家政banner" name="banner">
        <v-banner></v-banner>
      </el-tab-pane>
      <el-tab-pane label="人员管理" name="worker">
        <v-worker></v-worker>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script type="text/ecmascript-6">
import API from "../../common/js/API";
import http from "../../common/js/ajax";

import vBanner from '../views/homeBanner.vue'
import vWorker from '../views/homeWorker.vue'
export default {
  components: {
   vBanner,vWorker
  },
  props: {},
  data() {
    return {
      activeName: "banner"
    };
  },
  watch: {},
  computed: {},
  methods: {
    
  },
  mounted() {}
};
</script>
<style lang='stylus'  scoped>
@import '../../common/stylus/index.styl';

.wrapper {
  .el-tabs {
    width 90%;
    margin: 20px auto;
  }
}
</style>



