import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)
import index from '../components/pages/index'
import login from '../components/pages/login'
import welcome from '../components/pages/welcome'
import manage from '../components/pages/manage'
import teacher from '../components/pages/teacher'
import banner from '../components/pages/banner'
import home from '../components/pages/home'
import water from '../components/pages/water'
import changePass from '../components/pages/changePass'
import repair from '../components/pages/repair'

export default new Router({
  routes: [
      {
        path:"/login",
        component:login
      },
      {
        path:"/index",
        component:index,
        children:[
          {
            path:"",component:welcome,name:"欢迎"
          },
          {
            path:"manage",component:manage,name:"管理员管理"
          },
          {
            path:"teacher",component:teacher,name:"家教管理"
          },
          {
            path:"banner",component:banner,name:"banner管理"
          },
          {
            path:"water",component:water,name:"水站管理"
          },
          {
            path:"repair",component:repair,name:"维修管理"
          },
          {
            path:"home",component:home,name:"家政管理"
          },
          {
            path:"changePass",component:changePass,name:"修改密码"
          },
        ]
      },
      {
        path:"*",
        redirect:"/login"
      }
  ]
})



