<template>
  <div class="wrapper">
    <el-button class="add" @click="willAdd()">添加</el-button>
    <!-- list -->
    <el-table :data="tableData" height="250" border>
      <el-table-column label="序号">
        <template slot-scope="scope">{{scope.$index+1}}</template>
      </el-table-column>
      <el-table-column prop="name" label="姓名"></el-table-column>
      <el-table-column prop="des" label="属性"></el-table-column>
      <el-table-column label="时间">
        <template slot-scope="scope">{{scope.row.time |transTime}}</template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button @click="edit(scope.row.id)">查看</el-button>
          <v-del :id="scope.row.id" @del="del($event)"></v-del>
        </template>
      </el-table-column>
    </el-table>

    <!-- add -->
    <el-dialog title="添加|修改管理员" :visible.sync="dialogFormVisible">
      <el-form>
        <el-row class="row">
          <el-col :span="3" class="text-center label">账号</el-col>
          <el-col :span="21">
            <el-input :disabled="isEdit" autocomplete="off" v-model="user.name" placeholder="请输入账号"></el-input>
          </el-col>
        </el-row>
        <el-row class="row">
          <el-col :span="3" class="text-center label">密码</el-col>
          <el-col :span="21">
            <el-input
              autocomplete="off"
              :disabled="isEdit"
              show-password
              v-model="user.pass"
              placeholder="请输入密码"
            ></el-input>
          </el-col>
        </el-row>
        <el-row class="row" v-if="!isEdit">
          <el-col :span="3" class="text-center label">确认密码</el-col>
          <el-col :span="21">
            <el-input autocomplete="off" show-password v-model="confirm" placeholder="请输入确认密码"></el-input>
          </el-col>
        </el-row>
        <el-row class="row">
          <el-col :span="3" class="text-center label">描述</el-col>
          <el-col :span="21">
            <el-input autocomplete="off" v-model="user.des" placeholder="描述"></el-input>
          </el-col>
        </el-row>
        <el-row class="row">
          <el-col :span="3" class="text-center label">时间</el-col>
          <el-col :span="21">
            <el-date-picker v-model="user.time" type="date" placeholder="选择日期"></el-date-picker>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取消</el-button>
        <el-button type="primary" v-if="!isEdit" @click="add()">添加</el-button>
        <el-button type="primary" v-if="isEdit" @click="update()">修改</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script type="text/ecmascript-6">
import API from "../../common/js/API";
import http from "../../common/js/ajax";
export default {
  components: {},
  props: {},
  data() {
    return {
      tableData: [],
      dialogFormVisible: false,
      confirm: "",
      isEdit: false,
      user: {
        name: "",
        pass: "",
        des: "",
        time: ""
      }
    };
  },
  watch: {},
  computed: {},
  methods: {
    willAdd(){
      this.isEdit=false;
      this.dialogFormVisible = true
      this.user={
        name: "",
        pass: "",
        des: "",
        time: ""
      }
    },
    add() {
      this.user.time=new Date(this.user.time).getTime()
      delete this.user.id;
      this.isEdit = false;
      if (this.confirm != this.user.pass) {
        this.$message({
          message: "密码与确认密码不一致",
          type: "warning"
        });
        return;
      }
      http(
        this,
        API.addManage,
        "post",
        this.user,
        err => {
          this.$message({
            message: err.data.info,
            type: "warning"
          });
        },
        d => {
          this.$message({
            message: d.data.info,
            type: "success"
          });
          this.dialogFormVisible = false;
          this.init()
        }
      );
    },
    edit(id) {
      this.isEdit = true;
      this.dialogFormVisible = true;
      http(
        this,
        API.findManage,
        "post",
        { id },
        err => {
          this.$message({
            message: err.data.info,
            type: "warning"
          });
        },
        d => {
          this.user = d.data.data[0];
          this.user.time=new Date(parseInt(this.user.time))
        }
      );
    },
    update() {
      this.user.time=new Date(this.user.time).getTime()
      http(
        this,
        API.updateManage,
        "post",
        this.user,
        err => {
          this.$message({
            message: err.data.info,
            type: "warning"
          });
        },
        d => {
          this.$message({
            message: d.data.info,
            type: "success"
          });
          this.dialogFormVisible = false;
          this.init()
        }
      );
    },
    del(id) {
      console.log(id)
      http(
        this,
        API.delManage,
        "post",
        { id },
        err => {
          this.$message({
            message: err.data.info,
            type: "warning"
          });
        },
        d => {
          this.$message({
            message: d.data.info,
            type: "success"
          });
          this.init()
        }
      );
    },
    init() {
      http(
        this,
        API.findManage,
        "post",
        {},
        err => {
          this.$message({
            message: err.data.info,
            type: "warning"
          });
        },
        d => {
          this.tableData = d.data.data;
        }
      );
    }
  },
  mounted() {
    this.init()
  }
};
</script>
<style lang='stylus'  scoped>
@import '../../common/stylus/index.styl';

.wrapper {
  .el-input {
    width: 90%;
    margin: 0px auto;
  }

  .row {
    margin: 10px 0px;

    .label {
      line-height: 40px;
    }
  }
}
</style>



