<template>
  <div class="wrapper">
    <el-tabs v-model="activeName" >
      <el-tab-pane label="维修" name="repair">
        <v-repair></v-repair>
      </el-tab-pane>
      <el-tab-pane label="维修评论" name="comment">
        <v-comment></v-comment>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script type="text/ecmascript-6">
import API from "../../common/js/API";
import http from "../../common/js/ajax";

import vRepair from '../views/repair'
import vComment from '../views/repairComment'
export default {
  components: {
   vRepair,vComment
  },
  props: {},
  data() {
    return {
      activeName: "repair"
    };
  },
  watch: {},
  computed: {},
  methods: {
    
  },
  mounted() {}
};
</script>
<style lang='stylus'  scoped>
@import '../../common/stylus/index.styl';

.wrapper {
  .el-tabs {
    width 90%;
    margin: 20px auto;
  }
}
</style>



