<template>
  <div class="wrapper">
    <div class="center-box">
      <h3 class="text-center text-primary h2">登录</h3>
      <el-select v-model="user.type" placeholder="请选择角色">
        <el-option
          v-for="item in types"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>
      <el-input placeholder="请输入账号" v-model='user.name'  clearable></el-input>
      <el-input placeholder="请输入密码" v-model='user.pass' show-password  clearable></el-input>
      <div class="text-center">
        <el-button type='primary' @click='login()'>登录</el-button>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import API from '../../common/js/API'
import $http from '../../common/js/ajax'
export default {
  components: {},
  props: {},
  data() {
    return {
      user: {
        name: "",
        pass: "",
        type: ""
      },
      types: [
        {
          value: "0",
          label: "超级管理员"
        },
        {
          value: "1",
          label: "普通管理员"
        }
      ]
    };
  },
  watch: {},
  computed: {},
  methods: {
    login(){
      $http(this,API.login,'post',this.user,(err)=>{
        this.$message({
          message: err.data.info,
          type: 'warning'
        });
      },(d)=>{
        this.$message({
          message: d.data.info,
          type: 'success'
        });
        localStorage.setItem("username",this.user.name);
        localStorage.setItem("type",d.data.type)
        this.$router.replace("/index")
      })
    }
  },
  mounted() {}
};
</script>
<style lang='stylus' scoped>
@import '../../common/stylus/index.styl';

.wrapper {
  width: 100vw;
  height: 100vh;
  background: $second-color;
}
</style>



