<template>
  <div class="wrapper">
    <el-button class="add" @click="willAdd()">添加</el-button>
    <!-- list -->
    <el-table :data="tableData" height="300" border>
      <el-table-column label="序号">
        <template slot-scope="scope">{{scope.$index+1}}</template>
      </el-table-column>
      <el-table-column label="维修名称">
        <template slot-scope="scope">{{scope.row.name}}</template>
      </el-table-column>
      <el-table-column label="电话">
        <template slot-scope="scope">{{scope.row.tel}}</template>
      </el-table-column>
      <el-table-column label="类型">
        <template slot-scope="scope">{{scope.row.type}}</template>
      </el-table-column>

      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button @click="edit(scope.row.id)">查看</el-button>
          <v-del :id="scope.row.id" @del="del($event)"></v-del>
        </template>
      </el-table-column>
    </el-table>

    <!-- add -->
    <el-dialog title="添加维修" :visible.sync="dialogFormVisible">
      <el-form>
        <el-row class="row">
          <el-col :span="3" class="text-center label">名称</el-col>
          <el-col :span="21">
            <el-input autocomplete="off" v-model="user.name" placeholder="名称"></el-input>
          </el-col>
        </el-row>
        <el-row class="row">
          <el-col :span="3" class="text-center label">评分</el-col>
          <el-col :span="21">
            <el-input
              autocomplete="off"
              type="number"
              min="0"
              max="5"
              v-model="user.score"
              placeholder="0-5分之间"
            ></el-input>
          </el-col>
        </el-row>
        <el-row class="row">
          <el-col :span="3" class="text-center label">类型</el-col>
          <el-col :span="21">
            <el-input autocomplete="off" v-model="user.type" placeholder="维修类型"></el-input>
          </el-col>
        </el-row>
        <el-row class="row">
          <el-col :span="3" class="text-center label">点赞数量</el-col>
          <el-col :span="21">
            <el-input
              autocomplete="off"
              type="number"
              min="0"
              v-model="user.likeNum"
              placeholder="点赞数量"
            ></el-input>
          </el-col>
        </el-row>
        <el-row class="row">
          <el-col :span="3" class="text-center label">关注人数</el-col>
          <el-col :span="21">
            <el-input
              autocomplete="off"
              type="number"
              min="0"
              v-model="user.readNum"
              placeholder="关注人数"
            ></el-input>
          </el-col>
        </el-row>
        <el-row class="row">
          <el-col :span="3" class="text-center label">商家地址</el-col>
          <el-col :span="21">
            <el-input autocomplete="off" v-model="user.address" placeholder="商家地址"></el-input>
          </el-col>
        </el-row>

        <el-row class="row">
          <el-col :span="3" class="text-center label">距离</el-col>
          <el-col :span="21">
            <el-input autocomplete="off" v-model="user.len" placeholder="eg:4.2km"></el-input>
          </el-col>
        </el-row>
        <el-row class="row">
          <el-col :span="3" class="text-center label">商家信息</el-col>
          <el-col :span="21">
            <el-input autocomplete="off" v-model="user.info" placeholder="商家信息"></el-input>
          </el-col>
        </el-row>
        <el-row class="row">
          <el-col :span="3" class="text-center label">电话</el-col>
          <el-col :span="21">
            <el-input autocomplete="off" v-model="user.tel" placeholder="eg:400-100-1000"></el-input>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取消</el-button>
        <el-button type="primary" @click="add()" v-if="!isEdit">添加</el-button>
        <el-button type="primary" @click="update()" v-if="isEdit">修改</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script type="text/ecmascript-6">
import API from "../../common/js/API";
import http from "../../common/js/ajax";
export default {
  components: {},
  props: {},
  data() {
    return {
      tableData: [],
      dialogFormVisible: false,
      user: {
        name: "",
        score: "",
        type: "",
        address: "",
        tel: "",
        len: "",
        likeNum: "",
        readNum: "",
        info: ""
      },
      file: null,
      name: "",

      isEdit: false
    };
  },
  watch: {},
  computed: {},
  methods: {
    willAdd() {
      this.isEdit = false;
      this.dialogFormVisible = true;
      this.user = {
        name: "",
        score: "",
        type: "",
        address: "",
        tel: "",
        len: "",
        likeNum: "",
        readNum: "",
        info: ""
      };
    },
    add() {
      http(
        this,
        API.addRepair,
        "get",
        this.user,
        err => {
          this.$message({
            message: err.data.info,
            type: "warning"
          });
        },
        d => {
          this.$message({
            message: d.data.info,
            type: "success"
          });
          this.dialogFormVisible = false;
          this.init();
        }
      );
    },
    edit(id) {
      this.isEdit = true;
      this.dialogFormVisible = true;
      http(
        this,
        API.findRepair,
        "get",
        { id },
        err => {
          this.$message({
            message: err.data.info,
            type: "warning"
          });
        },
        d => {
          this.user = d.data.data[0];
        }
      );
    },
    del(id) {
      http(
        this,
        API.delRepair,
        "get",
        { id },
        err => {
          this.$message({
            message: err.data.info,
            type: "warning"
          });
        },
        d => {
          this.$message({
            message: d.data.info,
            type: "success"
          });
          this.init();
        }
      );
    },
    update() {
      http(
        this,
        API.updateRepair,
        "get",
        this.user,
        err => {
          this.$message({
            message: err.data.info,
            type: "warning"
          });
        },
        d => {
          this.$message({
            message: d.data.info,
            type: "success"
          });
          this.dialogFormVisible = false;
          this.init();
        }
      );
    },
    init() {
      http(
        this,
        API.findRepair,
        "get",
        {},
        err => {
          this.$message({
            message: err.data.info,
            type: "warning"
          });
        },
        d => {
          this.tableData = d.data.data;
        }
      );
    }
  },
  mounted() {
    this.init();
  }
};
</script>
<style lang='stylus'  scoped>
@import '../../common/stylus/index.styl';

.wrapper {
  .el-input {
    width: 90%;
    margin: 0px auto;
  }

  .el-table {
    width: 100%;
  }

  .row {
    margin: 10px 0px;

    .label {
      line-height: 40px;
    }
  }

  img {
    width: 200px;
    height: 100px;
  }
}
</style>



