import axios from 'axios'
function ajax(obj,url,method,params,errorCallBack,successCB){
    var paramsCan='params';
    if(method.toUpperCase()=='POST'){
        paramsCan='data'
    }
    axios({
        url:url,
        method:method,
        [paramsCan]:params,
    }).then(d=>{
        console.log("=============="+url+"================");
        console.log(d)
        if(d.data.code==-1){
            obj.$router.replace("/login");
            return;
        }
        if(d.data.isok){
            successCB(d)
        }else{
            errorCallBack(d)
        }
    })
}
export default ajax;


