<template>
  <div class="wrapper">
    <el-tabs v-model="activeName" >
      <el-tab-pane label="水站" name="water">
        <v-water></v-water>
      </el-tab-pane>
      <el-tab-pane label="水站评论" name="comment">
        <v-comment></v-comment>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script type="text/ecmascript-6">
import API from "../../common/js/API";
import http from "../../common/js/ajax";

import vWater from '../views/water'
import vComment from '../views/waterComment'
export default {
  components: {
   vWater,vComment
  },
  props: {},
  data() {
    return {
      activeName: "water"
    };
  },
  watch: {},
  computed: {},
  methods: {
    
  },
  mounted() {}
};
</script>
<style lang='stylus'  scoped>
@import '../../common/stylus/index.styl';

.wrapper {
  .el-tabs {
    width 90%;
    margin: 20px auto;
  }
}
</style>



