<template>
  <div class="wrapper">
    <header>
      <el-button class="exit" @click="exit()">退出登录</el-button>
      <h3 class="username">admin</h3>
    </header>
    <div class="main">
      <v-left></v-left>
      <v-right></v-right>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import API from "../../common/js/API";
import http from "../../common/js/ajax";
import vLeft from '../views/left'
import vRight from '../views/right'
export default {
  components: {
    vLeft,
    vRight
  },
  props: {},
  data() {
    return {};
  },
  watch: {},
  computed: {},
  methods: {
    exit(){
      http(this,API.exit,"get",{},err=>{},d=>{
        this.$router.replace("/login")
      })
    }
  },
  mounted() {}
};
</script>
<style lang='stylus'  scoped>
@import '../../common/stylus/index.styl';

.wrapper {
  height: 100vh;
  display flex;
  flex-direction column;
  header{
    height: 80px;
    background $select-color;
  }
  .main{
    flex: 1;
    display flex;
  }
  .exit{
    float right
    margin-top 20px;
    margin-right 50px;
  }
  .username{
    float right
    color #fff 
    font-size 30px;
    margin-right 50px;
    line-height: 80px;
  }
}
</style>



