
<template>
  <div class="up_load">
    <h3 class="show_box">+</h3>
    <img v-if="objImg.showImgUrl" class="up_img" :src="objImg.showImgUrl" alt />
    <input class="up_load_ipt" type="file" @change="change($event)" />
  </div>
</template>

<script type="text/ecmascript-6">
// 组件使用说明
// <v-upload :showImgUrl="showImgUrl" @changed='changed($event)'></v-upload>
// changed(e){
//       this.file=e.file;
//       this.name=e.name;
//     },

import API from "../../common/js/API";
import http from "../../common/js/ajax";
export default {
  components: {},
  props: ["objImg"],
  data() {
    return {
      file: null,
      name: ""
    };
  },
  watch: {},
  computed: {
    
  },
  methods: {
    change(e) {
      this.file = e.target.files[0];
      var extname = this.file.name.substr(this.file.name.lastIndexOf("."));
      if (
        extname != ".jpg" &&
        extname != ".png" &&
        extname != ".jpeg" &&
        extname != "gif"
      ) {
        this.$message({
          message: "文件格式不正确",
          type: "warning"
        });
        return;
      }
      this.name =
        new Date().getTime() +
        this.file.name.slice(this.file.name.lastIndexOf("."));
      var reader = new FileReader();
      reader.readAsDataURL(this.file);
      reader.onload = e => {
        this.objImg.showImgUrl = e.target.result;

        this.$emit("changed", { file: this.file, name: this.name });
      };
    }
  },
  mounted() {}
};
</script>
<style lang='stylus'  scoped>
@import '../../common/stylus/index.styl';

.wrapper {
}
</style>





