<template>
  <div class="wrapper">
    <el-button class="add" @click="willAdd()">添加</el-button>
    <!-- list -->
    <el-table :data="tableData" height="300" border>
      <el-table-column label="序号">
        <template slot-scope="scope">{{scope.$index+1}}</template>
      </el-table-column>
      <el-table-column label="图片" width="300">
        <template slot-scope="scope">
          <img :src="scope.row.img" alt />
        </template>
      </el-table-column>
      <el-table-column prop="num" label="报名人数"></el-table-column>
      <el-table-column label="授课机构" width="300">
        <template slot-scope="scope">
          <img :src="scope.row.teacherImg" alt />
        </template>
      </el-table-column>

      <el-table-column label="操作">
        <template slot-scope="scope">
          <v-del :id="scope.row.id" @del="del($event)"></v-del>
        </template>
      </el-table-column>
    </el-table>

    <!-- add -->
    <el-dialog title="添加家教排行" :visible.sync="dialogFormVisible">
      <el-form>
        <el-row>
          <el-col :span="12">
            <el-row class="row">
              <el-col :span="6" class="text-center label">图片</el-col>
              <el-col :span="18">
                 <v-upload :objImg="objImg" @changed='changed($event)'></v-upload>
              </el-col>
            </el-row>
          </el-col>
          <el-col :span="12">
            <el-row class="row">
              <el-col :span="6" class="text-center label">机构图片</el-col>
              <el-col :span="18">
                 <v-upload :objImg="objImg2" @changed='changed2($event)'></v-upload>
              </el-col>
            </el-row>
          </el-col>
        </el-row>

        <el-row class="row">
          <el-col :span="3" class="text-center label">报名人数</el-col>
          <el-col :span="21">
            <el-input autocomplete="off" v-model="user.num" placeholder="描述"></el-input>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取消</el-button>
        <el-button type="primary" @click="add()">添加</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script type="text/ecmascript-6">
import API from "../../common/js/API";
import http from "../../common/js/ajax";
export default {
  components: {},
  props: {},
  data() {
    return {
      tableData: [],
      dialogFormVisible: false,
      user: {
        img: "",
        num: "",
        teacherImg:""
      },
      file: null,
      name: "",
      file2: null,
      name2: "",
      objImg:{
        showImgUrl: "",
      },
      objImg2:{
        showImgUrl: ""
      }
      
    };
  },
  watch: {},
  computed: {},
  methods: {
    willAdd(){
      this.dialogFormVisible = true
      this.objImg.showImgUrl=""
      this.objImg2.showImgUrl=""
      this.user={
        img: "",
        num: "",
        teacherImg:""
      }
    },
    changed(e) {
      this.file = e.file;
      this.name = e.name;
    },
    changed2(e) {
      this.file2 = e.file;
      this.name2 = e.name;
    },

    add() {
      if (!this.file || !this.name||!this.file2 || !this.name2) {
        this.$message({
          message: "请选择图片",
          type: "warning"
        });
        return;
      }
      qiniu
        .upload(this.file, this.name, this.$store.state.token)
        .subscribe(res => {
          if (res.total.percent == 100) {
            this.user.img = this.$store.state.qiniu + this.name;
            qiniu
              .upload(this.file2, this.name2, this.$store.state.token)
              .subscribe(res => {
                if (res.total.percent == 100) {
                  this.user.teacherImg = this.$store.state.qiniu + this.name2;
                  http(
                    this,
                    API.addTeacherTop,
                    "get",
                    this.user,
                    err => {
                      this.$message({
                        message: err.data.info,
                        type: "warning"
                      });
                    },
                    d => {
                      this.$message({
                        message: d.data.info,
                        type: "success"
                      });
                      this.dialogFormVisible = false;
                      this.init();
                    }
                  );
                }
              });
          }
        });
    },
    del(id) {
      http(
        this,
        API.delTeacherTop,
        "get",
        { id },
        err => {
          this.$message({
            message: err.data.info,
            type: "warning"
          });
        },
        d => {
          this.$message({
            message: d.data.info,
            type: "success"
          });
          this.init();
        }
      );
    },
    init() {
      http(
        this,
        API.teacherTop,
        "get",
        {},
        err => {
          this.$message({
            message: err.data.info,
            type: "warning"
          });
        },
        d => {
          this.tableData = d.data.data;
        }
      );
    }
  },
  mounted() {
    this.init();
  }
};
</script>
<style lang='stylus'  scoped>
@import '../../common/stylus/index.styl';

.wrapper {
  .el-input {
    width: 90%;
    margin: 0px auto;
  }

  .el-table {
    width: 100%;
  }

  .row {
    margin: 10px 0px;

    .label {
      line-height: 40px;
    }
  }

  img {
    width: 200px;
    height: 100px;
  }
}
</style>



