<template>
  <div class="wrapper">
    <el-tabs v-model="activeName" >
      <el-tab-pane label="家教banner" name="banner">
        <v-banner></v-banner>
      </el-tab-pane>
      <el-tab-pane label="家教类型" name="type">
        <v-type></v-type>
      </el-tab-pane>
      <el-tab-pane label="家教排行" name="top">
        <v-top></v-top>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script type="text/ecmascript-6">
import API from "../../common/js/API";
import http from "../../common/js/ajax";

import vBanner from '../views/teacherBanner'
import vType from '../views/teacherType'
import vTop from '../views/teacherTop'
export default {
  components: {
    vBanner,
    vType,
    vTop
  },
  props: {},
  data() {
    return {
      activeName: "banner"
    };
  },
  watch: {},
  computed: {},
  methods: {
    
  },
  mounted() {}
};
</script>
<style lang='stylus'  scoped>
@import '../../common/stylus/index.styl';

.wrapper {
  .el-tabs {
    width 90%;
    margin: 20px auto;
  }
}
</style>



